import { Component, OnInit, Output, Input, EventEmitter, } from '@angular/core';
import { ActivatedRoute,ParamMap, Params,Router } from '@angular/router';
import { SelectItem, Message, AutoCompleteModule } from 'primeng/primeng';
import * as _ from 'lodash';
import { GroupAdminInfoBean, roleList } from './../../bean/custom-group.bean';
import { CusOperationHttpService } from './../../../http/custom-operation/custom-operation.http.service';
import { GroupGradeInfoBean } from './../../bean/custom-group.bean';

@Component({
    selector: 'customGroup-holder-approval',
    templateUrl: './customGroup-holder-approval.html',
    styleUrls: ['./customGroup-holder-approval.scss'],
    providers:[CusOperationHttpService]
})
export class customGroupHolderApprovalComponent implements OnInit {
    groupName;
    groupId;
    // 控件
    public location_params : any;
    public role_list : any;
    public msgs : any;
    public header_1 : any;
    public change_prev : any;
    public change_after : any;
    public remark : string;
    public grounpId : any;
    public grounpName : any;
    public isContactPerson : boolean;
    public groupGradeInfoBean : GroupGradeInfoBean=new GroupGradeInfoBean;
    //------------
    public institution: any;
    public staff: any;
    constructor(public httpService: CusOperationHttpService,
        public route : ActivatedRoute
    ) {
    }
    ngOnInit() {
        this.location_params = this.route.snapshot.params;
        this.header_1 = this.location_params.header_1;
        this.isContactPerson = this.location_params.roleType == 1 ? true : false;
        this.selectGroupAdminInfo(1);
        this.selectGroupAdminInfo(3);
    }
  
    selectGroupAdminInfo(statusId) {
        let params = {         
            groupId : this.route.snapshot.params['groupId'],
            roleType : this.route.snapshot.params['roleType'],
        }
        // 客户群管理人员再查询
        this.httpService.selectGroupAdminInfo(params).subscribe(data => {
            if (data.returnCode.code == 'success') {
                if(statusId == 1){
                    this.change_prev = [];
                    if(data.roleList != null){
                        data.roleList.forEach(item => {
                            this.change_prev.push({
                                userId : item.userId,
                                userName : item.userName,
                                groupZone : item.groupZone
                            })
                        });
                    }
                }else if(statusId == 3){
                    this.change_after = [];
                    if(data.roleList != null){
                        data.roleList.forEach(item => {
                            this.change_after.push({
                                userId : item.userId,
                                userName : item.userName,
                                groupZone : item.groupZone
                            })
                        });
                    }
                }
               
            } else {
                this.msgs = [];
                data.returnCode.code ? data.returnCode.code : '查询失败';
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用服务失败' }];
        })
    }
    confirm(apply_result){
        let params = {
            groupId : this.location_params.groupId,
            roleType : this.location_params.roleType,
            remark : this.remark,
            apprResult : apply_result,
            roleList : this.change_prev,          
            applyRoleList : this.change_after          
        }
         // 客户群管理人员变更申请
         this.httpService.updateGroupAdminStatus(params).subscribe(data => {
            if (data.returnCode.code == 'success') {   
                this.msgs = [];
                this.msgs = [{ severity: 'success', summary: '提示', detail: '变更成功' }];
            } else {
                this.msgs = [];
                data.returnCode.code ? data.returnCode.code : '查询失败';
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用服务失败' }];
        })
    }
    selectGroupGradeInfo() {
        // 客户群评级查询 
        this.httpService.selectGroupGradeInfo({groupId:this.location_params.groupId}).subscribe(data => {
          if (data.returnCode.code == 'success') {
            //传参
            this.groupGradeInfoBean = data;
          }
          else {
            this.msgs = [];
            data.returnCode.code ? data.returnCode.code : '查询失败！';
            this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }]
          }
        }, err => {
          this.msgs = [];
          this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
        })
      }
}