import { Component, OnInit, Output, Input, EventEmitter, } from '@angular/core';
import { ActivatedRoute, Params, Router } from '@angular/router';
import { SelectItem, Message, AutoCompleteModule } from 'primeng/primeng';
import * as _ from 'lodash';
import { GroupAdminInfoBean, CustGroupInfoBean, roleList } from './../../bean/custom-group.bean';
import { CusOperationHttpService } from './../../../http/custom-operation/custom-operation.http.service';
import { tableMessage } from 'app/@uisftech/common/constant/config';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';

@Component({
    selector: 'customGroup-admin-approval',
    templateUrl: './customGroup-admin-approval.html',
    styleUrls: ['./customGroup-admin-approval.scss'],
    providers: [CusOperationHttpService]
})
export class customGroupAdminApprovalComponent implements OnInit {
    //公共
    tableMesg: any = tableMessage;
    // 控件
    location_params: any;
    role_list: any;
    msgs: any;
    header_1: any;
    change_prev: any = [];
    change_after: any = [];
    remark: string;
    isContactPerson: boolean;
    //------------
    groupId: string;
    groupName: string;
    institution: any;
    staff: any;
    obj_info: any;
    orgId: any;
    CustGroupInfoBean: CustGroupInfoBean = new CustGroupInfoBean;
    isYuanG: boolean = false;
    isCust: boolean = false;
    theRequest: any = [];
    arrayList: any = [];

    constructor(public httpService: CusOperationHttpService,
        public route: ActivatedRoute,
        public commfunc: Commfunc,
    ) {
    }
    ngOnInit() {

        let url = location.href;  //获取url中？后面的参数
        if (url.indexOf(";") != -1) {
            let str = url.substr(1);
            let strs = str.split(";");
            for (let i = 0; i < strs.length; i++) {
                this.theRequest[strs[i].split("=")[0]] = (strs[i].split("=")[1]);
            }
        }

        this.theRequest.doResult = decodeURI(this.theRequest.doResult);
        this.location_params = this.route.snapshot.params;
        // this.header_1 = this.location_params.header_1;
        this.isContactPerson = this.location_params.roleType == 1 ? true : false;
        let i = this.location_params.roleType;
        if (i == 1 || i == 2 || i == 4) {
            this.arrayList = JSON.parse(this.commfunc.getSessionDataCH('groupContactor'));
        } else {
            this.arrayList = JSON.parse(this.route.snapshot.params['dataMap']);
        }
        
        if (i == 4) {
            this.header_1 = '群评价人';
        } else if (i == 5) {
            this.header_1 = '群主';
        } else if (i == 3) {
            this.header_1 = '群指导师傅';
        } else if (i == 1) {
            this.header_1 = '群联系人';
        } else {
            this.header_1 = '群介绍人';
        }
        if (i == 1 || i == 5 || i == 3) {
            this.isYuanG = true;
            this.isCust = false;
        } else {
            this.isYuanG = false;
            this.isCust = true;
        }
        // this.selectGroupAdminInfo(1); // 调整前
        // this.selectGroupAdminInfo(3)
        // if (this.theRequest.doResult && this.theRequest.doResult == '1') { // 判断-是否否决
        //     this.selectGroupAdminInfo(4); // 否决后
        // } else {
        //     this.selectGroupAdminInfo(3); // 未否决
        // }

        if (this.arrayList.batchOld.length > 0) {
            this.arrayList.batchOld.forEach(element => {//调整前
                this.change_prev.push({
                    userId: element.userId,
                    userName: element.userName,
                    groupZone: element.groupZone
                })
            });
        } else {
            setTimeout(() => {
                this.change_prev = [];
            })
        }

        if (this.arrayList.batchNew.length > 0) {
            this.arrayList.batchNew.forEach(element => {//调整后
                this.change_after.push({
                    userId: element.userId,
                    userName: element.userName,
                    groupZone: element.groupZone
                })
            });

            this.remark = this.arrayList.batchNew[0].updateReason;//调整原因
        } else {
            setTimeout(() => {
                this.change_after = [];
            })
        }

        this.selectCustGroupInfo()
    }

    selectGroupAdminInfo(statusId) {
        let params = {}
        if (statusId == '1') {
            params = {
                groupId: this.route.snapshot.params['groupId'],
                roleType: this.route.snapshot.params['roleType'],
                statusId: statusId,
            }
        }
        if (statusId == '3') {
            params = {
                batchId: this.arrayList.batchId,
                groupId: this.arrayList.groupId,
                roleType: this.arrayList.roleType,
            }
        }
        // 客户群管理人员再查询
        this.httpService.selectGroupAdminInfo(params).subscribe(data => {
            if (data.returnCode.code == 'success') {
                if (statusId == 1) {
                    this.change_prev = [];
                    if (data.roleList != null) {
                        data.roleList.forEach(item => {
                            this.change_prev.push({
                                userId: item.userId,
                                userName: item.userName,
                                groupZone: item.groupZone
                            })
                        });
                    }
                } else if (statusId == 3) {
                    this.change_after = [];
                    if (data.roleList != null) {
                        data.roleList.forEach(item => {
                            this.change_after.push({
                                userId: item.userId,
                                userName: item.userName,
                                groupZone: item.groupZone
                            })
                        });
                    }
                    this.remark = data.updateReason; // 调整原因
                    // if(this.remark == undefined || this.remark == '' || this.remark == null){
                    //     this.selectGroupAdminInfo(4);
                    // }
                }
                // else if (statusId == 4) {
                //     this.change_after = [];
                //     if (data.roleList != null) {
                //         data.roleList.forEach(item => {
                //             this.change_after.push({
                //                 userId: item.userId,
                //                 userName: item.userName,
                //                 groupZone: item.groupZone
                //             })
                //         });
                //     }
                //     this.remark = data.updateReason; // 调整原因
                // }

            } else {
                this.msgs = [];
                data.returnCode.code ? data.returnCode.code : '查询失败';
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用服务失败' }];
        })
    }
    confirm(apply_result) {
        let params = {
            groupId: this.location_params.groupId,
            roleType: this.location_params.roleType,
            remark: this.remark,
            apprResult: apply_result,
            roleList: this.change_prev,
            applyRoleList: this.change_after
        }
        // 客户群管理人员变更申请
        this.httpService.updateGroupAdminStatus(params).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.msgs = [];
                this.msgs = [{ severity: 'success', summary: '提示', detail: '变更成功' }];
            } else {
                this.msgs = [];
                data.returnCode.code ? data.returnCode.code : '查询失败';
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用服务失败' }];
        })
    }

    selectCustGroupInfo() {
        let params = {
            groupId: this.location_params.groupId
        }
        // 客户群信息查询
        this.httpService.selectCustGroupInfo(params).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.CustGroupInfoBean = data;
                this.groupId = data.groupId;
                this.groupName = data.groupName;
                this.queryPartyGroupListByPartyId(this.CustGroupInfoBean.orgId);
            }
            else {
                this.msgs = [];
                data.returnCode.code ? data.returnCode.code : '查询失败！';
                this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
            }
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用服务失败！' }];
        })
    }

    // 根据机构id查询机构名称
    queryPartyGroupListByPartyId(orgId) {
        let params = {
            partyIdList: [orgId]
        }
        this.httpService.queryPartyGroupListByPartyId(params).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.orgId = data.partyGroupList[0].groupName;
            }
        })
    }
}