import { Component, OnInit } from '@angular/core';
import { CusOperationHttpService } from '../../../http/custom-operation/custom-operation.http.service';
import { Observable } from 'rxjs/Rx';
import { GroupGradeInfoBean } from './../../bean/custom-group.bean';
import { SelectItem, Message, ConfirmationService, ConfirmDialogModule } from 'primeng/primeng';
import { ActivatedRoute, Params, Router } from '@angular/router';

@Component({
  selector: 'customGroup-rating-approval2',
  templateUrl: './customGroup-rating-approval2.html',
  styleUrls: ['./customGroup-rating-approval2.scss'],
  providers: [ConfirmationService, CusOperationHttpService],

})
export class GroupApproval2Component implements OnInit {

  public cunstomGroupName: string = "XXX客户群";
  public cunstomRatingAdjust: string = "群评级调整";
  public cunstomRating: string = "群评级";
  public msgs: Message[] = [];  //提示信息
  public groupGradeInfoBean: GroupGradeInfoBean = new GroupGradeInfoBean();// 客户群评级查询
  public adjustGroupLevelSel: string;//调整星级
  public rateFlag: boolean = true;//是否已评级，默认已评级
  public selGroupLevel: any;//当前星级
  // 管道码值
  // public CODEVALUE: any =  CODEVALUE;
  // 控件
  sysGroupLevel: SelectItem[] = [];//系统星级
  groupLevel: SelectItem[] = [];//当前星级
  adjustGroupLevel: SelectItem[] = [];//调整星级

  groupLevel2: string; //调整星级
  // 链接中间量
  public groupId: any = '';//群号
  public groupName: any = '';//群名称
  public params: any;

  constructor(
    public httpService: CusOperationHttpService,
    public routerInfo: ActivatedRoute,
    public router: Router,
    public confirmationService: ConfirmationService,
  ) { }

  ngOnInit() {
    // 初始化
    // route传参
    this.groupId = this.routerInfo.snapshot.params.groupId;//群名称    groupLevel  
    this.groupLevel2 = this.routerInfo.snapshot.params.groupLevel;//群名称    groupLevel  
    // 当前群评星级,调整返回
    this.groupLevel = [
      { label: '无', value: '' },
      { label: '一星', value: '1' },
      { label: '二星', value: '2' },
      { label: '三星', value: '3' },
      { label: '四星', value: '4' },
      { label: '五星', value: '5' },
    ];
    // 系统建议星级
    this.sysGroupLevel = [
      { label: '无', value: '' },
      { label: '一星', value: '1' },
      { label: '二星', value: '2' },
      { label: '三星', value: '3' },
      { label: '四星', value: '4' },
      { label: '五星', value: '5' },
    ];
    //  // 调整星级
    this.adjustGroupLevel = [
      { label: '无', value: '' },
      { label: '一星', value: '1' },
      { label: '二星', value: '2' },
      { label: '三星', value: '3' },
      { label: '四星', value: '4' },
      { label: '五星', value: '5' },
    ];
    // this.params = this.routerInfo.snapshot.params;
    // this.preSet();
    this.selectGroupGradeInfo();
    this.selectCustGroupInfo();
  }
  preSet() {

    // 调试用
    this.selectGroupGradeInfo();
    // this.rateFlag = false;
  }
  selectGroupGradeInfo() {
    // 客户群评级查询 
    this.httpService.selectGroupGradeInfo({ groupId: this.groupId }).subscribe(data => {
      if (data.returnCode.code == 'success') {
        //传参
        this.groupGradeInfoBean = data;
      }
      else {
        this.msgs = [];
        data.returnCode.code ? data.returnCode.code : '查询失败！';
        this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }]
      }
    }, err => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
    })
  }

  selectCustGroupInfo() {
    let params = {
      groupId: this.groupId
    }
    // 客户群信息查询
    this.httpService.selectCustGroupInfo(params).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.groupName = data.groupName;
      }
      else {
        this.msgs = [];
        data.returnCode.code ? data.returnCode.code : '查询失败！';
        this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
      }
    }, err => {
      this.msgs = [];
      this.msgs = [{ severity: 'error', summary: '提示', detail: '调用服务失败！' }];
    })
  }
}
