import { Routes, RouterModule } from '@angular/router';
import { ModuleWithProviders } from '@angular/core';
import { GroupApproval2Component } from "app/pages/tzb/custom/custom-group/custom-group-view/customGroup-rating-approval2/customGroup-rating-approval2";
import { GroupApprovalComponent } from "app/pages/tzb/custom/custom-group/custom-group-view/customGroup-rating-approval/customGroup-rating-approval";
import { customGroupHolderApprovalComponent } from "app/pages/tzb/custom/custom-group/custom-group-view/customGroup-holder-approval/customGroup-holder-approval";
import { MarketSchemeCheckComponent } from 'app/pages/tzb/custom/custom-group//market-scheme/market-scheme-check/market-scheme-check.component'
import { customGroupListApprovalComponent } from "app/pages/tzb/custom/custom-group/custom-group-view/customGroup-list-approval/customGroup-list-approval";
import { customGroupAdminApprovalComponent } from 'app/pages/tzb/custom/custom-group/custom-group-view/customGroup-admin-approval/customGroup-admin-approval';
import { CreateCustomGroupApprovalComponent } from 'app/pages/tzb/exam-appr-comp/custom/custom-group/custom-group.component';
import { MessageApproveComponent } from 'app/@uisftech/view/message-approve/message-approve.component';

export const routes: Routes = [
  {
    //群名单增加
    path: 'custApprove/:applyType/:batchId/:groupId',
    component: customGroupListApprovalComponent
  },
  {
    //群营销方案修改
    path: 'a/b/c/d/e/f/g/j/groupPlan/:groupId/:planId/:applyType',
    component: MarketSchemeCheckComponent
  },
  {
    //创建客户群10
    path: 'a/b/c/d/e/f/g/j/:groupId/:applyType',
    component: CreateCustomGroupApprovalComponent
  },
  {
    //群星级修改8
    path: 'approval/b/c/d/e/:groupId/:groupLevel/:groupAfterLevel',
    component: GroupApprovalComponent
  },
  {
    //修改客户群9
    path: 'a/b/c/d/e/f/:groupId/:roleType/:role',
    component: customGroupHolderApprovalComponent
  },
  {
    //修改客户群7
    path: 'a/b/c/d/e/:groupId/:roleType',
    component: customGroupAdminApprovalComponent
  },
  {
    //群星级新增6
    path: 'approval2/b/c/d/:groupId/:groupLevel',
    component: GroupApproval2Component
  },
  {
    //群短信4
    path: 'a/b/:custGroupReportId/:groupId',
    component: MessageApproveComponent
  }

  ,

];

export const OtherCusGroupRouting: ModuleWithProviders = RouterModule.forChild(routes);
