import { Component, OnInit, Output, Input, EventEmitter, } from '@angular/core';
import { ActivatedRoute, Params,Router } from '@angular/router';
import { SelectItem, Message, AutoCompleteModule } from 'primeng/primeng';
import * as _ from 'lodash';
import { GroupAdminInfoBean, roleList } from './../../bean/custom-group.bean';
import { CusOperationHttpService } from './../../../http/custom-operation/custom-operation.http.service';

@Component({
    selector: 'customGroup-rating-approval',
    templateUrl: './customGroup-rating-approval.html',
    styleUrls: ['./customGroup-rating-approval.scss']
})
export class GroupApprovalComponent implements OnInit {
    // 控件
    public location_params : any;
    public role_list : any;
    public msgs : any;
    public header_1 : any;
    public change_prev : any;
    public change_after : any;
    public groupLevel : any;
    public remark : string;
    public isContactPerson : boolean;
    //------------
    public institution: any;
    public staff: any;
    //
    public obj_info : any = {
        groupLevel:'',
        groupAfterLevel:'',
        endDate:'',
        groupName : '',
        groupId : '',
        ownerName : ''
    };
    constructor(public httpService: CusOperationHttpService,
        public route : ActivatedRoute
    ) {
    }
    ngOnInit() {
        this.location_params = this.route.snapshot.params;
        this.selectGroupGradeInfo();
        this.selectCustGroupInfo();
        this.groupLevel = ['无', '一星', '二星','三星','四星','五星'];
    }
  
    selectGroupGradeInfo() {
        let params = {
            groupId : this.location_params.groupId,
        }
        // 客户群管理人员再查询
        this.httpService.selectGroupGradeInfo(params).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.obj_info = {
                    groupLevel:this.groupLevel[data.groupLevel],
                    groupAfterLevel:this.groupLevel[this.location_params.groupAfterLevel],
                    endDate:data.endDate,
                }
            } else {
                this.msgs = [];
                data.returnCode.code ? data.returnCode.code : '查询失败';
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用服务失败' }];
        })
    }
    selectCustGroupInfo() {
        let params = {
          groupId: this.location_params.groupId
        }
        // 客户群信息查询
        this.httpService.selectCustGroupInfo(params).subscribe(data => {
          if (data.returnCode.code == 'success') {
            this.obj_info['groupName'] = data.groupName;
            this.obj_info['groupId'] = this.location_params.groupId;
            this.obj_info['ownerName'] = data.ownerName;
          }
          else {
            this.msgs = [];
            data.returnCode.code ? data.returnCode.code : '查询失败！';
            this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
          }
        }, err => {
          this.msgs = [];
          this.msgs = [{ severity: 'error', summary: '提示', detail: '调用服务失败！' }];
        })
      }
}