import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { CustomeHttpService } from 'app/@uisftech/view/http/uisftech-view.http.service';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { ActivatedRoute, ParamMap } from '@angular/router';
@Component({
    selector: 'app-message-approve',
    template: `
    <div class="ui-g-12">
    <header-title [Info]="title"></header-title>
    <div class="ui-g-12">
        <div class="ui-g-2">
            基本资料：
            <hr>
        </div>
    </div>
    <div class="ui-g-12">
        <div class="ui-g-6 ui-g-offset-1">
            <div class="ui-g-2" style="padding:0px;">
                <label for="">群号：</label>
            </div>
            <div class="ui-g-2">
                <input type="text" pInputText [(ngModel)]="location_params.groupId" disabled="true">
                <!-- <input type="text" pInputText disabled="true"> -->
            </div>
        </div>
        <div class="ui-g-3">
            <div class="ui-g-4" style="padding:0px;">
                <label for="">群名称：</label>
            </div>
            <div class="ui-g-8" style="padding:0px;">
                <input type="text" pInputText [(ngModel)]="info.groupName" disabled="true">
                <!-- <input type="text" pInputText disabled="true"> -->
            </div>
        </div>
    </div>
    <div class="ui-g-12">
        <div class="ui-g-6">
            <div class="ui-g-4" style="padding:0px;">
                <label for="">发送时间：</label>
            </div>
            <div class="ui-g-8" style="padding:0px;">
                <input type="text" pInputText [(ngModel)]="info.createdTime" disabled="true">
                <!-- <input type="text" pInputText disabled="true"> -->
            </div>
        </div>
    </div>
    <div class="ui-g-12">
        <div class="ui-g-12">
            <div class="ui-g-4" style="padding:0px;">
                <label for="">短信内容：</label>
            </div>
            <div class="ui-g-8" style="padding:0px;">
                    <input id="input" type="textarea" pInputTextarea style="height:60px;" [(ngModel)]="info.messageTempleContent" disabled="true">
                <!-- <input type="text" pInputText disabled="true"> -->
            </div>
        </div>
    </div>
    <div class="ui-g-12 table">
		<p-dataTable [value]="info.resultPageList" scrollable="true" scrollHeight="350px" scrollWidth="'100%'">
			<p-headerColumnGroup>
				<p-row>
					<p-column header="客户号" colspan="1"></p-column>
					<p-column header="客户名称" colspan="1"></p-column>
					<p-column header="联系电话" colspan="1"></p-column>
				</p-row>
			</p-headerColumnGroup>
			<p-column field="custNo"></p-column>
			<p-column field="custName"></p-column>
			<p-column field="custPhone"></p-column>
		</p-dataTable>
		<!-- <p-paginator rows="{{pageSize}}" totalRecords="{{totalNum}}" pageLinkSize="3" [rowsPerPageOptions]="[10,20,50,200]"
		    (onPageChange)="paginate($event)"></p-paginator> -->
	</div>
</div>`,
    providers: [Commfunc, CustomeHttpService]
})

export class MessageApproveComponent implements OnInit {
    msgs: any = [];
    param: any = {};
    title　: any;
    info: any = {
        groupId: '',
        groupName: '',
        createdTime: '',
        templateId: '',
        messageTempleTotalType: '',
        resultPageList: [],
        messageTempleContent: ''
    }
    location_params: any;
    constructor(
        public router: Router,
        public route: ActivatedRoute,
        public Commfunc: Commfunc,
        public CustomeHttpService: CustomeHttpService
    ) { }
    ngOnInit() {
        this.location_params = this.route.snapshot.params;
        this.param = {
            custGroupReportId: this.location_params.custGroupReportId,
            groupId: this.location_params.groupId,
            pageSize: 1000,
            pageNum: 1
        }
        this.queryCustGroupApproval();
        //   this.selectCustGroupInfo();
    }

    //查询短信审批信息
    queryCustGroupApproval() {
        let successFnc = data => {
            this.info = data;
            let params = {
                templeId: data.templateId,
                messageTempleTotalType: data.messageTempleTotalType
            }
            this.viewInfoTemplate(params)
        }
        this.Commfunc.interfaceComFnc(this.CustomeHttpService.queryCustGroupApproval(this.param), successFnc, this.msgs)
    }

    viewInfoTemplate(params) {
        let successFnc = data => {
            this.info.messageTempleContent = data.messageTempleContent;
        }
        this.Commfunc.interfaceComFnc(this.CustomeHttpService.viewInfoTemplate(params), successFnc, this.msgs)
    }
    // selectCustGroupInfo() {
    //     let params = {
    //         groupId : this.location_params.groupId
    //     }
    //     // 客户群信息查询
    //     this.CustomeHttpService.selectCustGroupInfo(params).subscribe(data => {
    //         if (data.returnCode.code == 'success') {
    //             this.info.groupId = data.groupId;
    //             this.info.groupName= data.groupName;
    //         }
    //         else {
    //             this.msgs = [];
    //             data.returnCode.code ? data.returnCode.code : '查询失败！';
    //             this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
    //         }
    //     }, err => {
    //         this.msgs = [];
    //         this.msgs = [{ severity: 'error', summary: '提示', detail: '调用服务失败！' }];
    //     })
    // }
}